import React, { Component } from "react";
import logo from "./logo.svg";
import "./App.css";
import Search from "./Components/Search/Search";
import Table from "./Components/Table/Table";


const DEFAULT_QUERY = 'lego';

const PATH_BASE = 'https://hn.algolia.com/api/v1';
const PATH_SEARCH = '/search';
const PARAM_SEARCH = 'query=';

const url = `${PATH_BASE}${PATH_SEARCH}?${PARAM_SEARCH}${DEFAULT_QUERY}`;


const list = [
  {
    title: "React",
    url: "https://facebook.github.io/react/",
    author: "Jordan Walke",
    num_comments: 3,
    points: 4,
    objectID: 0
  },
  {
    title: "Redux",
    url: "https://github.com/reactjs/redux",
    author: "Dan Abramov, Andrew Clark",
    num_comments: 2,
    points: 5,
    objectID: 1
  }
];

const isSearched = searchTerm => item =>
  item.title.toLowerCase().includes(searchTerm.toLowerCase());

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      result: null,
      searchTerm: DEFAULT_QUERY,
    };

    this.setSearchTopStories - this.setSearchTopStories.bind(this);
    this.onSearchChange = this.onSearchChange.bind(this);
    this.onDismiss = this.onDismiss.bind(this);
  }

  setSearchTopStories(result) {
    this.setState({result});
  }

  componentDidMount = async () => {
    const searchTerm = this.state;
    const response = await fetch("/api/posts");
    const json = await response.json();
    console.log(json.posts);
    if(json.posts) this.setState({loading: false, result: json.posts})
  }

  onDismiss(id) {
    const isNotId = item => item.objectID !== id;
    const updatedHits = this.state.result.hits.filter(isNotId);
    this.setState({
      result: {...this.state.result, hits: updatedHits}
    });
  }

  onSearchChange(event) {
    this.setState({ searchTerm: event.target.value });
  }
  render() {
    const { searchTerm, result } = this.state;

    if(!result) { return null; }
    return (
      <div className="page">
        <div className="interactions">
          <Search value={searchTerm
          } onChange={this.onSearchChange}> Search </Search>
        </div>
      { result ? <Table list={result.posts} pattern={searchTerm} onDismiss={this.onDismiss} /> : null}
      </div>
    );
  }
}

export { isSearched };
export default App;



